<template>
    <div>
        <button @click="clickHandler()" disabled id="installBtn">설치 버튼</button>
    </div>
  </template>
  
  <script>
  import { onMounted, onBeforeUnmount } from 'vue';
  
  export default {
    name: 'HelloWorld',
    setup() {
  
    //   const receiveMessage = () => {
    //     // console.log('message', event.data);
    //     console.log("message")
    //   }
    const clickHandler=()=>{
        window.Android.installTMap();
    }
    const isGetAuth=(res)=>{
      if(res=='true'){
        let text="You have auth"
        window.Android.showToast(text)
      }else{

      }
    }
      const callJsFunction=(str)=>{
        if(str =='true'){
            installBtn.disabled=false;
        }
        else if(str=='false' || str == 'error')
        console.log(str);
      }

      onMounted(() => {
        // window.addEventListener('message', receiveMessage(), false);
        window.callJsFunction=callJsFunction;
        window.isGetAuth=isGetAuth;
      }) 
  
      onBeforeUnmount(() => {
        // window.removeEventListener('message', receiveMessage());
        window.callJsFunction=null;
        window.isGetAuth=null;
      })
        return {callJsFunction,clickHandler}
    }

  }
  </script>
  
